"use client";
import React, { useRef } from "react";
import {
  Flex,
  IconButton,
  Box,
  Tooltip,
  Alert,
  DialogBody,
  DialogFooter,
  DialogHeader,
  DialogContent,
  AlertDialogOverlay,
  Button,
} from "@chakra-ui/react";
import { FaSnapchatGhost, FaTiktok, FaInstagram } from "react-icons/fa";
import { IoLogOut } from "react-icons/io5";
import { useRouter } from "next/navigation";
import useColorModeStyles from "@/utils/useColorModeStyles";

const Sidebar = () => {
  const { bg, hoverColor } = useColorModeStyles();
  const cancelRef = useRef(null);
  const router = useRouter();

  const [isLogoutOpen, setLogoutOpen] = React.useState(false);

  const handleRedirect = (path: string) => {
    router.push(path);
  };

  const handleLogout = () => {
    localStorage.clear();
    setLogoutOpen(false);
    router.push("/login");
  };

  return (
    <>
      <Flex
        direction="column"
        align="center"
        bg={bg}
        p={4}
        width="100%"
        height="100%"
        gap={4}
      >
        {/* Snapchat */}
        <Tooltip label="Snapchat" aria-label="Snapchat">
          <IconButton
            icon={<FaSnapchatGhost />}
            aria-label="Snapchat"
            variant="ghost"
            onClick={() => handleRedirect("/snapchat")}
            _hover={{ color: hoverColor }}
          />
        </Tooltip>

        <Box height="1px" width="20px" bg="gray.400" />

        {/* TikTok */}
        <Tooltip label="TikTok" aria-label="TikTok">
          <IconButton
            icon={<FaTiktok />}
            aria-label="TikTok"
            variant="ghost"
            onClick={() => handleRedirect("/tiktok")}
            _hover={{ color: hoverColor }}
          />
        </Tooltip>

        <Box height="1px" width="20px" bg="gray.400" />

        {/* Instagram */}
        <Tooltip label="Instagram" aria-label="Instagram">
          <IconButton
            icon={<FaInstagram />}
            aria-label="Instagram"
            variant="ghost"
            onClick={() => handleRedirect("/instagram")}
            _hover={{ color: hoverColor }}
          />
        </Tooltip>

        <Box height="1px" width="20px" bg="gray.400" />

        {/* Logout */}
        <Tooltip label="Logout" aria-label="Logout">
          <IconButton
            icon={<IoLogOut style={{ transform: "scaleX(-1)" }} />}
            aria-label="Logout"
            variant="ghost"
            onClick={() => setLogoutOpen(true)}
            _hover={{ color: hoverColor }}
          />
        </Tooltip>
      </Flex>

      {/* Logout Confirmation Modal */}
      {isLogoutOpen && (
        <Alert
          status="warning"
          variant="subtle"
          flexDirection="column"
          alignItems="center"
          justifyContent="center"
          textAlign="center"
          height="200px"
        >
          <AlertDialogHeader>Confirm Logout</AlertDialogHeader>
          <AlertDialogBody>
            Are you sure you want to log out?
          </AlertDialogBody>
          <AlertDialogFooter>
            <Button onClick={() => setLogoutOpen(false)}>Cancel</Button>
            <Button colorScheme="red" onClick={handleLogout} ml={3}>
              Logout
            </Button>
          </AlertDialogFooter>
        </Alert>
      )}
    </>
  );
};

export default Sidebar;
